import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { compose } from 'redux';
import { setContent, setImageUrl, setPrice, setTherapist, setTime } from '../../../store/actions/therapistActions';
import './TherapistSummary.css';

const TherapistSummary = props => {
  // console.log(props);
  const { therapist, setTherapist, setImageUrl, setContent, setTime, setPrice } = props;
  const onButtonCLick = () => {
    setTherapist(therapist.title);
    setTime(therapist.time);
    setPrice(therapist.price);
    props.history.push('/therapist/' + therapist.title + '/book/date');
  };

  const onImageClick = () => {
    setImageUrl(therapist.imgUrl);
    setTime(therapist.time);
    setPrice(therapist.price);
    setContent(therapist.body);
    props.history.push('/therapist/' + therapist.title);
  };

  return (
    <div className="card">
      <img
        className="img-fluid therapist-img"
        src={therapist.imgUrl}
        onClick={() => onImageClick()}
        alt="logo"
      />
      <div className="summary-body">
        <h6 className="summary-title">{therapist.title}</h6>
        <div className="summary-text">
          <p className="summary-time">{therapist.time}</p>
          <p className="vertical-line"></p>
          <p className="summary-price">&#8377; {therapist.price}</p>
        </div>
        <button onClick={() => onButtonCLick()} className="summary-btn">
          Book Now
        </button>
      </div>
    </div>
  );
};

const mapDispatchToProps = dispatch => {
  return {
    setTherapist: therapist => dispatch(setTherapist(therapist)),
    setImageUrl: imageUrl => dispatch(setImageUrl(imageUrl)),
    setTime: time => dispatch(setTime(time)),
    setPrice: price => dispatch(setPrice(price)),
    setContent: content => dispatch(setContent(content))
  };
};

export default compose(withRouter, connect(null, mapDispatchToProps))(TherapistSummary);
